import React, { useEffect, useMemo, useState } from 'react'
import { Modal, Text, Flex, Box, Button, Skeleton , Link,Image, useMatchBreakpoints} from '@pancakeswap/uikit'
import useTheme from 'hooks/useTheme'
import { useTranslation } from 'contexts/Localization'
import Balance from 'components/Balance'
import { createClient } from '@supabase/supabase-js';
import { ButtonPro, ButtonText, Container, HeadingText, ImageWrapper, SalesSectionProps, ScrollBox, SubHeadingText, Word } from './Styled'

const supabaseUrl = process.env.REACT_APP_SUPABASE_URL;
const supabaseKey = process.env.REACT_APP_SUPABASE_KEY;
const supabase = createClient(supabaseUrl, supabaseKey);

const SalesSection: React.FC<SalesSectionProps> = (props) => {
  const { t } = useTranslation()
  const { headingText, bodyText, reverse, primaryButton, secondaryButton, images } = props
  const headingTranslatedText = t(headingText)
  const bodyTranslatedText = t(bodyText) 
  const { theme } = useTheme()
  const [allin2, setAllin2] = useState(null);
  const [lastUpdated, setLastUpdated] = useState(null);

  
  // Supabase-bl val lekrdezs, a timestampal egytt
  useEffect(() => {
    const fetchAllin2FromSupabase = async () => {
      const { data, error } = await supabase
        .from('allin_data')
        .select('allin_value, updated_at')
        .order('updated_at', { ascending: false })
        .limit(1); // Csak a legutbbi adatot krjk le
  
      if (error) {
        console.error('Error fetching allin2 from Supabase:', error);
      } else if (data.length > 0) {
        // Belltjuk az allin2-t a legfrissebb rtkre, ha van
        if (data[0].allin_value !== null && !Number.isNaN(data[0].allin_value)) {
          setAllin2(data[0].allin_value);
          setLastUpdated(data[0].updated_at); // Elmentjk az utols frisstsi idt
        } else {
          // Ha nincs adat, akkor alaprtelmezett rtket lltunk be (pl. 0)
          setAllin2(0);
        }
      } else {
        setAllin2(0);
      }
    };

    fetchAllin2FromSupabase();
  }, []);



                // BE2MINING  + POOLOK + FARMOK + LOCKED
  const { isXs, isSm, isMd, isLg, isXl } = useMatchBreakpoints()
  const isMobile = !isXs
  const isMobile2 = !isSm
  const isMobile3 = !isMd
  const isMobile4 = !isLg
  const isMobile5 = !isXl
 // Egyedi azonost generlsa minden szhoz
 const generateUniqueKey = (word: string, index: number) => `${word}-${index}`;

 // A f komponens, amely a szveget s az alfejlc szveget megjelenti
 const Component = ({ headingTranslatedText, bodyTranslatedText }) => (
   <>
     <HeadingText>
       {headingTranslatedText.split(' ').map((word, index) => {
         // Kiemeljk az 1., 10., 11., s 12. szavakat
         const isHighlighted = [0, 9, 10, 11].includes(index);
         return (
           <Word key={generateUniqueKey(word, index)} isHighlighted={isHighlighted}>
             {word}
           </Word>
         );
       })}
     </HeadingText>
     <SubHeadingText>{bodyTranslatedText}</SubHeadingText>
   </>
 );
 
  return (
    <Container>
    <Flex flexDirection="column" mt='25px'  mb='30px'>
      <Flex
        flexDirection={['column-reverse', null, null, reverse ? 'row-reverse' : 'row']}
        alignItems={['flex-end', null, null, 'center']}
        justifyContent="center"
      > 
                 <Flex
          height={['230px', null, null, '100%']}
          width={['230px', null, null, '100%']}
          flex={[null, null, null, '1']}
          mb={['0px', null, null, '0']}   
        >
{!isMobile &&  <ImageWrapper><img src="/images/pixel/G3.png" alt="ifo bunny" width="450px" height="450px"/> </ImageWrapper>}
{!isMobile2 &&   <ImageWrapper><img src="/images/pixel/G1.png" alt="ifo bunny" width="450px" height="450px"/> </ImageWrapper>}
{!isMobile3 &&   <ImageWrapper><img src="/images/pixel/G1.png" alt="ifo bunny" width="450px" height="450px"/> </ImageWrapper>}
{!isMobile4 &&  <ImageWrapper><img src="/images/pixel/G3.png" alt="ifo bunny" width="450px" height="450px"/> </ImageWrapper>}
{!isMobile5 &&   <ImageWrapper><img src="/images/pixel/G3.png" alt="ifo bunny" width="450px" height="450px"/> </ImageWrapper>}
         </Flex>
        <Flex
          flexDirection="column"
          flex="1" 
          ml={[null, null, null, reverse && '64px']} 
          mr={[null, null, null, !reverse && '64px']}
          alignSelf={['flex-start', null, null, 'center']}
        >  
            <Component headingTranslatedText={headingTranslatedText} bodyTranslatedText={bodyTranslatedText} />

                 <Flex mb='10px'>
            <Link mr="16px" external={primaryButton.external} href="/swap">
              <ButtonPro  variant="secondary" height="40px" mb="16px" width="100px">
              <ButtonText color="text" bold fontSize="16px">
          {t('TRADE')}
        </ButtonText>
              </ButtonPro>
            </Link>
            <Link  mr="16px"  external={secondaryButton.external} href="/pools/all">
            <ButtonPro variant="gold" height="40px" mb="16px" width="120px">
              <ButtonText color="gold" bold fontSize="16px">
          {t('EARN')}
        </ButtonText>
              </ButtonPro>
            </Link> 
          </Flex>
          <ScrollBox>
          <Flex justifyContent="center" alignItems="center">
          <Flex flexDirection="column" alignItems="center" >
      <ImageWrapper>
        <img src="/images/wallet/000.png" alt="ifo bunny" width="40px" height="40px"/>
      </ImageWrapper>
      </Flex >
          <Flex flexDirection="column" alignItems="left" >
    <Text color="primary"  fontSize="13px" mr="5px">
        {t('Total Value Locked')}
      </Text>
      {allin2 ? (
        <Balance fontSize="18px" prefix="$" bold decimals={0} value={allin2} />
      ) : (
        <Skeleton width={60} height={16} />
      )}
    </Flex >
    </Flex>
    </ScrollBox>
        </Flex>
      </Flex>
    </Flex>
          </Container>
  )
}
export default SalesSection


